<template>
  <div class="userRegisterWx-container">
    <!---->
    <div class="user-container" v-if="!isSale">
      <p class="textPrompt"> abcwww.yayiabc.com</p>
      <mt-field @click.native="focus" class="must-fill" label="" v-model="trueName" placeholder=""></mt-field>
      <mt-radio title="" align="right" v-model="sex" :options="['', '']"></mt-radio>
      <mt-radio title="" align="right" v-model="type" :options="['', '']"></mt-radio>
      <!---->
      <div class="personal-container" v-if="type === ''">
        <mt-field @click.native="focus" label="" v-model="birthday" placeholder="">
          <mt-button type="primary" size="small" @click="openPicker('birthDatePicker')">+</mt-button>
        </mt-field>
        <mt-field @click.native="focus" class="must-fill" label="" v-model="companyName" placeholder=""></mt-field>
        <mt-field @click.native="focus" class="must-fill" label="" v-model="part" placeholder="">
          <mt-button type="primary" size="small" @click="openPicker('cityAddressPicker')">+</mt-button>
        </mt-field>
        <mt-field @click.native="focus" class="must-fill" label="" v-model="workAddress" placeholder=""></mt-field>
        <pic-upload @picupload="savePicUrl('doctorPic',$event)" :picResources="doctorPic" title="" :isMust="true"></pic-upload>
      </div>
      <!---->
      <div class="organization-container" v-if="type === ''">
        <mt-field @click.native="focus" label="" v-model="birthday" placeholder="">
          <mt-button type="primary" size="small" @click="openPicker('birthDatePicker')">+</mt-button>
        </mt-field>
        <mt-field @click.native="focus" class="must-fill" label="" v-model="companyName" placeholder=""></mt-field>
        <mt-field @click.native="focus" class="must-fill" label="" v-model="part" placeholder="">
          <mt-button type="primary" size="small" @click="openPicker('cityAddressPicker')">+</mt-button>
        </mt-field>
        <mt-field @click.native="focus" class="must-fill" label="" v-model="workAddress" placeholder=""></mt-field>
        <pic-upload @picupload="savePicUrl('medicalLicense',$event)" :picResources="medicalLicense" title="" :isMust="true"></pic-upload>
        <pic-upload @picupload="savePicUrl('businessLicense',$event)" :picResources="businessLicense" title="" :isMust="true"></pic-upload>
        <pic-upload @picupload="savePicUrl('taxRegistration',$event)" :picResources="taxRegistration" title="" :isMust="true"></pic-upload>
        <pic-upload @picupload="savePicUrl('openingPermit',$event)" :picResources="openingPermit" title=""></pic-upload>
        <pic-upload @picupload="savePicUrl('doctorPic',$event)" :picResources="doctorPic" title=""></pic-upload>
        <pic-upload @picupload="savePicUrl('radiologicalPermit',$event)" :picResources="radiologicalPermit" title=""></pic-upload>
        <pic-upload @picupload="savePicUrl('idCardPositive',$event)" :picResources="idCardPositive" title=""></pic-upload>
        <pic-upload @picupload="savePicUrl('idCardOtherside',$event)" :picResources="idCardOtherside" title=""></pic-upload>

      </div>
    </div>

    <!---->
    <div class="sale-container" v-if="isSale">
      <p class="textPrompt"> www.yayiabc.com/saleslog</p>
      <div class="name">
        <mt-field @click.native="focus" class="must-fill" label="" v-model="trueName" placeholder=""></mt-field>
      </div>
      <div class="sex">
        <mt-radio title="" align="right" v-model="sex" :options="['', '']"></mt-radio>
      </div>
      <mt-field @click.native="focus" class="must-fill" label="" v-model="idCard" placeholder=""></mt-field>
      <mt-field @click.native="focus" class="must-fill" label="" v-model="workUnit" placeholder=""></mt-field>
      <mt-field @click.native="focus" class="must-fill" label="" v-model="workPosition" placeholder=""></mt-field>
      <mt-field @click.native="focus" class="must-fill" label="" v-model="part" placeholder="">
        <!--<mt-palette-button content="+"-->
        <!--:mainButtonStyle="mainBtnStyle"-->
        <!--ref="addressBtn" @expanded="openPicker('cityAddressPicker')"></mt-palette-button>-->
        <mt-button type="primary" size="small" @click="openPicker('cityAddressPicker')">+</mt-button>
      </mt-field>
      <mt-field @click.native="focus" class="must-fill" label="" v-model="address" placeholder=""></mt-field>
      <mt-field @click.native="focus" label="" v-model="referrals" placeholder=""></mt-field>
      <mt-field @click.native="focus" label="" v-model="birthday" placeholder="">
        <!--<mt-palette-button content="+" :mainButtonStyle="mainBtnStyle" ref="birthBtn"-->
        <!--@expanded="openPicker('birthDatePicker')"></mt-palette-button>-->
        <mt-button type="primary" size="small" @click="openPicker('birthDatePicker')">+</mt-button>
      </mt-field>
      <mt-field @click.native="focus" label="" v-model="education" placeholder=""></mt-field>
      <mt-field @click.native="focus" label="" v-model="weChar" placeholder=""></mt-field>
      <mt-field @click.native="focus" label="" v-model="email" placeholder=""></mt-field>

      <div class="postalType-container ">
        <h3 class="title must-fill"></h3>
        <mt-radio title="" align="right" v-model="postalType" :options="['', '']"></mt-radio>
        <div class="pay-container" v-if="postalType === ''">
          <mt-field @click.native="focus" class="must-fill" label="" v-model="openName" placeholder=""></mt-field>
          <mt-field @click.native="focus" class="must-fill" label="" v-model="accountNumber" placeholder=""></mt-field>
        </div>
        <div class="bank-container" v-if="postalType === ''">
          <mt-field @click.native="focus" class="must-fill" label="" v-model="bankName" placeholder=""></mt-field>
          <mt-field @click.native="focus" class="must-fill" label="" v-model="openName" placeholder=""></mt-field>
          <mt-field @click.native="focus" class="must-fill" label="" v-model="accountNumber" placeholder=""></mt-field>
        </div>
      </div>
    </div>

    <mt-button class="enter-btn" type="primary" @click="enter" size="large"></mt-button>

    <!---->
    <birth-date-picker ref="birthDatePicker" @birthdatechange="saveBirthDate"></birth-date-picker>
    <!---->
    <address-picker ref="cityAddressPicker" @addresschange="savePart"></address-picker>
  </div>
</template>

<script type="text/ecmascript-6">
  import picUpload from '../pic-upload.vue'
  import addressPicker from '../addressPicker.vue'
  import birthDatePicker from '../birthDatePicker.vue'
  import { Toast } from 'mint-ui'
  import { GET_UPLOAD_TOKEN, UPDATE_USER_INFO, UPDATE_SALE_INFO } from '../../../vuex/types'
  import { tokenMethods } from '../../../vuex/util'


  export default {
    name: 'userRegisterWx',
    data() {
      return {
        isBindSale: false,
        phone: '',
        salePhone: '',
        saleName: '',
        token: '',
        isSale: false,
        trueName: '',
        sex: '',
        type: '',
        workUnit: '',                               //
        companyName: '',                            //
        part: '',                                   // 
        workAddress: '',                            // 
        address: '',                                // 
        cityOpen: true,                             //
        postalType: '',                        //
        birthday: '',                               //
        email: '',                                  //
        weChar: '',                                 //
        education: '',                              //
        workPosition: '',                           //      
        idCard: '',                                 //  
        referrals: '',                               //
        bankName: '',                               //  
        openName: '',                               //
        accountNumber: '',                          // 
        number: '',                                 //
        entry: '',
        /**/
        businessLicense: '',                        //
        medicalLicense: '',                         //
        taxRegistration: '',                        //
        doctorPic: '',                              //
        openingPermit: '',                          //
        radiologicalPermit: '',                     //
        idCardOtherside: '',                        //
        idCardPositive: ''                         //
      }
    },
    components: { picUpload, addressPicker, birthDatePicker },
    methods: {
      focus(event) {
        let target = ''
        if (event.target.className === 'mint-cell-wrapper') {
          target = event.target
          // console.log(event.target)
        }
        else if (event.target.className === 'mint-cell-title') {
          target = event.target.parentNode
          // console.log(event.target.parentNode)
        } else if (event.target.className === 'mint-cell-text') {
          target = event.target.parentNode.parentNode
          // console.log(event.target.parentNode.parentNode)
        } else {
          return false
        }
        target.children[1].children[0].focus()
      },
      openPicker(type) {
        this.$refs[type].open()
      },
      savePart(e) {
        let part = e.split(' ').join(',')
        this.part = part
      },
      saveBirthDate(e) {
        this.birthday = e
      },
      savePicUrl(type, e) {
        switch (type) {
          case 'doctorPic':
            this.doctorPic = e
            break
          case 'businessLicense':
            this.businessLicense = e
            break
          case 'medicalLicense':
            this.medicalLicense = e
            break
          case 'taxRegistration':
            this.taxRegistration = e
            break
          case 'openingPermit':
            this.openingPermit = e
            break
          case 'radiologicalPermit':
            this.radiologicalPermit = e
            break
          case 'idCardPositive':
            this.idCardPositive = e
            break
          case 'idCardOtherside':
            this.idCardOtherside = e
            break
          default:
            break
        }
      },
      enter() {
        let params = {} //
        //
        if (this.isSale) {
          //
          switch (true) {
            case !this.trueName:
              Toast({ message: '', duration: 4000 });
              return
            case !this.idCard:
              Toast({ message: '', duration: 4000 });
              return
            case !this.workUnit:
              Toast({ message: '', duration: 4000 });
              return
            case !this.workPosition:
              Toast({ message: '', duration: 4000 });
              return
            case !this.part:
              Toast({ message: '', duration: 4000 });
              return
            case !this.address:
              Toast({ message: '', duration: 4000 });
              return
            default:
              break
          }
          params.trueName = this.trueName
          params.openid = this.openid
          params.phone = this.phone
          params.sex = (this.sex === '' ? 1 : 2)
          params.idCard = this.idCard
          params.workUnit = this.workUnit
          params.workPosition = this.workPosition
          params.part = this.part
          params.address = this.address
          params.referrals = this.referrals
          params.birthday = this.birthday
          params.education = this.education
          params.weChar = this.weChar
          params.email = this.email
          params.number = this.number //
          params.postalType = this.postalType
          //
          if (this.postalType === '') {
            //            
            switch (true) {
              case !this.openName:
                Toast({ message: '', duration: 4000 });
                return
              case !this.accountNumber:
                Toast({ message: '', duration: 4000 });
                return
              default:
                break
            }
            params.openName = this.openName
            params.accountNumber = this.accountNumber
          }
          else {
            //
            switch (true) {
              case !this.openName:
                Toast({ message: '', duration: 4000 });
                return
              case !this.accountNumber:
                Toast({ message: '', duration: 4000 });
                return
              case !this.bankName:
                Toast({ message: '', duration: 4000 });
                return
              default:
                break
            }
            params.openName = this.openName
            params.accountNumber = this.accountNumber
            params.bankName = this.bankName
          }
          //todo  token
          this.$store.dispatch(UPDATE_SALE_INFO, params).then(res => {
            tokenMethods.setToken((this.token || res.token))
            Toast({ message: '', duration: 4000 })
            this.$router.replace({
              path: '/wx_user/bindinfo',
              query: { role: 'sale', phone: this.phone, token: (this.token || res.token) }
            })
          })
        }
        else {
          //
          if (this.type === '') {
            //todo 
            switch (true) {
              case !this.trueName:
                Toast({ message: '', duration: 4000 });
                return
              case !this.birthday:
                Toast({ message: '', duration: 4000 });
                return
              case !this.companyName:
                Toast({ message: '', duration: 4000 });
                return
              case !this.part:
                Toast({ message: '', duration: 4000 });
                return
              case !this.workAddress:
                Toast({ message: '', duration: 4000 });
                return
              case !this.medicalLicense:
                Toast({ message: '', duration: 4000 });
                return
              case !this.businessLicense:
                Toast({ message: '', duration: 4000 });
                return
              case !this.taxRegistration:
                Toast({ message: '', duration: 4000 });
                return
              default:
                break
            }
            //todo type
            params.trueName = this.trueName
            params.phone = this.phone
            params.birthday = this.birthday
            params.sex = (this.sex === '' ? 2 : 1)
            params.number = this.number
            params.openid = this.openid
            params['certification.type'] = 2
            params['certification.companyName'] = this.companyName
            params['certification.part'] = this.part
            params['certification.workAddress'] = this.workAddress
            params['certification.medicalLicense'] = this.medicalLicense
            params['certification.businessLicense'] = this.businessLicense
            params['certification.taxRegistration'] = this.taxRegistration
            //
            params['certification.openingPermit'] = this.openingPermit
            params['certification.doctorPic'] = this.doctorPic
            params['certification.radiologicalPermit'] = this.radiologicalPermit
            params['certification.idCardPositive'] = this.idCardPositive
            params['certification.idCardOtherside'] = this.idCardOtherside
            console.info('', params)
          }
          else {
            //
            switch (true) {
              case !this.trueName:
                Toast({ message: '', duration: 4000 });
                return
              case !this.companyName:
                Toast({ message: '', duration: 4000 });
                return
              case !this.birthday:
                Toast({ message: '', duration: 4000 });
                return
              case !this.part:
                Toast({ message: '', duration: 4000 });
                return
              case !this.workAddress:
                Toast({ message: '', duration: 4000 });
                return
              case !this.doctorPic:
                Toast({ message: '', duration: 4000 });
                return
              default:
                break
            }
            params.trueName = this.trueName
            params.phone = this.phone
            params.openid = this.openid
            params.birthday = this.birthday
            params.sex = (this.sex === '' ? 2 : 1)
            params['certification.type'] = 1
            params['certification.companyName'] = this.companyName
            params['certification.part'] = this.part
            params['certification.workAddress'] = this.workAddress
            params['certification.doctorPic'] = this.doctorPic
            params.number = this.number
            console.info('', params)
          }
          this.$store.dispatch(UPDATE_USER_INFO, params).then(res => {
            //todo bindInfo
            console.info('', params)
            Toast({ message: '', duration: 4000 });
            this.$router.replace({
              path: '/wx_user/bindinfo',
              query: {
                role: 'doctor',
                phone: this.phone,
                isBindSale: this.isBindSale,
                token: (this.token || res.data.token),
                entry: this.entry,
                salePhone: this.salePhone,
                saleName: this.saleName
              }
            })
          })
        }
      },
      _init() {
        //
        this.entry = this.$route.query.entry
        this.openid = this.$route.query.openid
        //
        this.isSale = this.wxUserInfo.isSale
        if (!this.isSale) {
          //
          this._userInit()
        } else {
          this._saleInit()
        }
      },
      _userInit() {
        //
        let userInfo = this.wxUserInfo
        console.info('', userInfo)
        //
        if (userInfo.isBindSale) {
          //
          this.isBindSale = true
          this.saleName = userInfo.saleBindInfo.trueName
          this.salePhone = userInfo.saleBindInfo.phone
        }
        this.trueName = userInfo.trueName
        this.token = userInfo.token
        this.phone = userInfo.phone || this.$route.query.phone
        this.birthday = userInfo.birthday
        this.sex = (userInfo.sex === 2 ? '' : '')
        this.companyName = userInfo.companyName
        this.part = userInfo.part
        this.workAddress = userInfo.workAddress
        this.number = userInfo.number
        if (userInfo.type === 1) {
          this.type = ''
          this.doctorPic = userInfo.doctorPic
        }
        else {
          this.type = ''
          this.medicalLicense = userInfo.medicalLicense
          this.doctorPic = userInfo.doctorPic
          this.businessLicense = userInfo.businessLicense
          this.idCardOtherside = userInfo.idCardOtherside
          this.idCardPositive = userInfo.idCardPositive
          this.openingPermit = userInfo.openingPermit
          this.radiologicalPermit = userInfo.radiologicalPermit
          this.taxRegistration = userInfo.taxRegistration
        }
      },
      _saleInit() {
        let saleInfo = this.wxUserInfo
        this.trueName = saleInfo.trueName
        this.token = saleInfo.token
        this.phone = saleInfo.phone || this.$route.query.phone
        this.sex = (saleInfo.sex === 2 ? '' : '')
        this.idCard = saleInfo.idCard
        this.workUnit = saleInfo.workUnit
        this.workPosition = saleInfo.workPosition
        this.part = saleInfo.part
        this.address = saleInfo.address
        this.referrals = saleInfo.referrals
        this.birthday = saleInfo.birthday
        this.education = saleInfo.education
        this.weChar = saleInfo.weChar
        this.email = saleInfo.email
        this.number = saleInfo.number //
        if (saleInfo.postalType === '') {
          //
          this.postalType = ''
          this.openName = saleInfo.openName
          this.accountNumber = saleInfo.accountNumber
        }
        else {
          this.postalType = ''
          this.bankName = saleInfo.bankName
          this.openName = saleInfo.openName
          this.accountNumber = saleInfo.accountNumber
        }
      }
    },
    computed: {
      wxUserInfo() {
        return this.$store.state.wx.wxUserInfo
      }
    },
    created() {
      //token,
      this.$store.dispatch(GET_UPLOAD_TOKEN).then(res => {
        this.qiNiuToken = {
          token: res.msg
        }
      })
      this._init()
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "../../../common/sass/factory";

  .userRegisterWx-container {
    padding: 0 px2vw(66);
    font-size: px2vw(30);
    .textPrompt {
      font-size: px2vw(30);
      font-weight: 600;
      color: $themeColorHover;
      margin-bottom: px2vw(40);
    }
    .mint-radiolist-title {
      font-size: px2vw(30);
    }
    .mint-cell-wrapper {
      font-size: px2vw(27);
    }
    .sale-container {
      .textPrompt {
        font-size: px2vw(30);
      }
    }
    .enter-btn {
      margin: px2vw(80) 0;
    }
    .postalType-container {
      .title {
        text-indent: px2vw(20);
        margin-top: px2vw(50);
        padding-bottom: px2vw(10);
        font-size: px2vw(30);
        border-bottom: 1px solid #ccc;
        &.must-fill:before {
          top: px2vw(-10);
          left: px2vw(-20);
        }
      }
    }
    /**/
    .must-fill {
      position: relative;
      &:before {
        content: '*';
        position: absolute;
        z-index: 1993;
        color: red;
        top: px2vw(20);
        left: px2vw(0);
      }
    }
  }
</style>



